import React, {Component} from 'react';
import './style.css';
import FadeIn from './Fade-in';
import FadeOut from './Fade-out'


class Preview extends Component{
    constructor(props) {
        super(props);
        this.state = {
            text: ""
        };
        this.change();
    }
    change() {
        let str = " 2s91s ";
        let i = 0;
        let x = "";

        let interval = setInterval(()=>{
            x += str[i];
            this.setState({
                text: x
            });
            i++;
            if(i===str.length) {
                clearInterval(interval);
                    this.setState({
                        text: <FadeIn text={"Welcome home"}/>
                    });
                    setTimeout(() => {
                        this.setState({
                            text: <FadeOut text={"Welcome home"}/>
                        });
                        setTimeout(() => {
                            this.setState({
                                text: ""
                            });
                            this.props.loadPage();
                            console.log('finish')
                        },2000);
                    },2600)
            }
        },100+Math.random()*400);
    };
    render() {
        return (
            <div className="main">
                    <div>{this.state.text}</div>
            </div>
        )
    }
}
 export default Preview


