import React, {Component} from 'react';
import Preview from './Preview/Preview';
import FadeIn from './Preview/Fade-in';
import Home from './home/home'

import './style.css';
class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            load: <Preview loadPage={this.loadPage}/>
        };
    }
    loadPage = () => {
        this.setState({
            load: <FadeIn text={<Home/>}/>
        })
    };
    render() {
        return (
            <div>
                {this.state.load}
            </div>
        )
    }
}

export default App;


