import React, {Component} from 'react';
import gif from  '../media/ezgif.com-video-to-gif.gif';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import Home from '../Preview/Preview'

class home extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
        <BrowserRouter>
            <div>
                <div>
                    <img className="gif-bg" src={gif} alt=""/>
                    <div className="container">
                        <ul>
                            <Link className="a" to="/"><li className="home">home</li></Link>
                            <Link className="a" to="/about"><li className="about">about</li></Link>
                            <Link className="a" to="/shop"><li className="shop">shop</li></Link>
                            <Link className="a" to="/donate"><li className="donate">donate</li></Link>
                        </ul>
                    </div>
                </div>
                <div>
                    <Route path="/about" component={home}/>
                    <Route path="/about" component={Home}/>
                    <Route path="/shop" component={Home}/>
                    <Route path="/donate" component={Home}/>
                </div>
            </div>
        </BrowserRouter>
    )}
}
export default home


