import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

function Fade_out ({text}) {
    return(
        <ReactCSSTransitionGroup transitionName="out" transitionAppear={true}
                                 transitionAppearTimeout={2000} transitionLeave={true}>
            <p>{text}</p>
        </ReactCSSTransitionGroup>
    )
}
export default Fade_out


